.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1{
  font-size: 2.1em;
  font-weight: 600;
  color: green;
  font-family: 'Poppins';
}
h4{
  font-family: 'Poppins';
  color:green;
padding-right: 30px;
}
nav{
  margin:0 2em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
ul{
  display:flex;
  justify-content: flex-end;
}
li{
  font-family: 'Poppins';
  list-style-type: none;
padding:30px;
}
a{
  font-size: 1.4em;
font-weight: 400;
  text-decoration: none;
  color: black;
}
a:hover{
  color: green;
}
button{
  color:white;
  font-weight: 700;
border: none;
border-radius: 50px;
background-color: green;
padding:0.7em;
font-family: 'Poppins';
}
button:hover{
 opacity: 0.8;
}